import {
  CLOSE_DOWNLOAD_DIALOG,
  DOWNLOAD_DATA,
  OPEN_DOWNLOAD_DIALOG,
  SET_DOWNLOAD_VALUE,
} from "../constants";
import { objToURLParams } from "../utils";

export const openDownloadDialog = (workspaceId) => ({
  type: OPEN_DOWNLOAD_DIALOG,
  payload: { workspaceId },
});

export const closeDownloadDialog = () => ({
  type: CLOSE_DOWNLOAD_DIALOG,
});

export const setDownloadValueByType = (value, type) => ({
  type: SET_DOWNLOAD_VALUE,
  payload: { value, type },
});

export const downloadData = () => async (dispatch, getState) => {
  const state = getState();
  const { values, workspaceId, apiEndpoint } = state.DownloadProps;
  const params = objToURLParams(values);

  dispatch({
    type: DOWNLOAD_DATA,
    payload: {},
    CallAPI: `${workspaceId}${apiEndpoint}${params}`,
  });
};
