import React, { useRef } from "react";
import cn from "classnames";
import { useDetectOutsideClick } from "../../../../../../hooks/useOutsideClick";
import ArrowImg from "../../../../../../assets/images/Plot_Streams_Arrow.png";
import styles from "./Streams.module.css";
import Stream from "./Stream";
import { createStructuredSelector } from "reselect";
import { connect } from "react-redux";
import { plotStreamByIdSelector } from "../../../../../../redux/PlotsProps/selectors";
import { workSpacePlotStreams } from "../../../../../../redux/WorkSpacesProps/selectors";
import PropTypes from "prop-types";

const Streams = ({ plotId, workspaceId, selectedStream, streamsProps }) => {
  const dropdownRef = useRef(null);
  const [isActive, setIsActive] = useDetectOutsideClick(dropdownRef, false);
  const onClick = () => setIsActive(!isActive);

  return (
    <div className={styles.Root}>
      <button onClick={onClick} className={styles.MenuTrigger}>
        <div className={styles.MenuTriggerText}>
          {streamsProps && streamsProps.byId[selectedStream].text}
        </div>
        <div
          className={cn(styles.MenuTriggerIcon, {
            [styles.LessIcon]: isActive,
          })}
        >
          <img alt="" src={ArrowImg} />
        </div>
      </button>
      <ul
        ref={dropdownRef}
        className={cn(styles.Menu, { [styles.active]: isActive })}
      >
        {streamsProps &&
          Object.keys(streamsProps.byId).map((streamId) => (
            <Stream
              key={streamId}
              streamProps={streamsProps.byId[streamId]}
              isSelected={selectedStream === streamId}
              closeDropdown={onClick}
              plotId={plotId}
              workspaceId={workspaceId}
            />
          ))}
      </ul>
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  selectedStream: plotStreamByIdSelector,
  streamsProps: workSpacePlotStreams,
});

Streams.propTypes = {
  plotId: PropTypes.string.isRequired,
  workspaceId: PropTypes.string.isRequired,
  selectedStream: PropTypes.string.isRequired,
  streamProps: PropTypes.shape({
    byId: PropTypes.object.isRequired,
  }),
};

export default connect(mapStateToProps)(Streams);
