import React from 'react';
import cn from 'classnames';

import styles from './Period.module.css';
import { createStructuredSelector } from 'reselect';
import { connect } from 'react-redux';
import { plotPeriodByIdSelector } from '../../../../../../../redux/PlotsProps/selectors';
import { setPlotPeriod } from '../../../../../../../redux/PlotsProps/actions';
import PropTypes from 'prop-types';

const Period = ({ currentPeriod, buttonProps, setPeriod }) => {
  const handleClick = () => {
    setPeriod(buttonProps.value);
  };

  return (
    <div
      className={cn(styles.Root, {
        [styles.active]: currentPeriod === buttonProps.value,
      })}
      onClick={handleClick}
    >
      {buttonProps.text || '? HOURS'}
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  currentPeriod: plotPeriodByIdSelector,
});

const mapDispatchToProps = (dispatch, ownProps) => ({
  setPeriod: (period) =>
    dispatch(setPlotPeriod(ownProps.plotId, period, ownProps.workspaceId)),
});

Period.propTypes = {
  currentPeriod: PropTypes.number.isRequired,
  buttonProps: PropTypes.shape({
    value: PropTypes.number.isRequired,
    text: PropTypes.string.isRequired,
  }),
  setPeriod: PropTypes.func.isRequired,
};

export default connect(mapStateToProps, mapDispatchToProps)(Period);
