import React from 'react';
import cn from 'classnames';
import styles from './RegimeButton.module.css';
import {
  setSpecification,
  updateSpecInputs,
} from '../../../../../redux/SpecDialog/actions';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

const RegimeButton = ({
  text,
  specificationId,
  isChecked,
  setNewRegime,
  updateSpecs,
  workspaceId,
}) => {
  return (
    <>
      <button
        className={cn(styles.Root, {
          [styles.Disabled]: false,
        })}
        onClick={() => {
          setNewRegime();
          updateSpecs(workspaceId, specificationId);
        }}
      >
        <svg
          className={cn(styles.Marker, {
            [styles.checkedMarker]: isChecked,
          })}
          width="51px"
          height="45px"
          viewBox="0 0 51 45"
        >
          <g transform="matrix(1,0,0,1,-2785.93,-2818.93)">
            <g transform="matrix(1,0,0,1,2801,2841.18)">
              <path
                d="M0,-0.35L6.796,6.825L20,-7.175"
                style={{
                  fill: 'none',
                  fillRule: 'nonzero',
                  stroke: 'rgb(255,83,73)',
                  strokeWidth: '3px',
                }}
              />
            </g>
          </g>
        </svg>
        <p className={styles.Text}>{text}</p>
      </button>
    </>
  );
};

const mapDispatchToProps = (dispatch, ownProps) => ({
  setNewRegime: () => dispatch(setSpecification(ownProps.specificationId)),
  updateSpecs: (id, regime) => dispatch(updateSpecInputs(id, regime)),
});

RegimeButton.propTypes = {
  text: PropTypes.string.isRequired,
  specificationId: PropTypes.string.isRequired,
  isChecked: PropTypes.bool.isRequired,
  setNewRegime: PropTypes.func.isRequired,
  updateSpecs: PropTypes.func.isRequired,
  workspaceId: PropTypes.string.isRequired,
};

export default connect(null, mapDispatchToProps)(RegimeButton);
