import { createSelector } from 'reselect';

export const downloadPropsSelector = createSelector(
  (state) => state.DownloadProps,
  (props) => props
);

export const downloadContextRenderPropsSelector = createSelector(
  downloadPropsSelector,
  (state) => state.WorkSpacesProps,
  (downloadProps, workspacesProps) => {
    const contextRenderProps =
      workspacesProps?.byId[downloadProps.workspaceId]?.dialogProps?.download;

    if (contextRenderProps) {
      return {
        renderOrder: contextRenderProps.renderOrder,
        buttonGroups: contextRenderProps.buttonGroups,
      };
    } else {
      return null;
    }
  }
);

export const downloadContextRenderOrderSelector = createSelector(
  downloadContextRenderPropsSelector,
  (renderProps) => renderProps?.renderOrder
);

export const downloadContextBtnGroupsSelector = createSelector(
  downloadContextRenderPropsSelector,
  (renderProps) => renderProps?.buttonGroups
);
