import React from "react";
import SettingsBar from "./SettingsBar";
import styles from "./InfoWidget.module.css";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { createStructuredSelector } from "reselect";
import { workspaceWidgetsSelector } from "../../../../redux/WorkSpacesProps/selectors";

const InfoWidget = ({ workspaceId, widgets }) => {
  return (
    <div className={styles.Root}>
      <SettingsBar workspaceId={workspaceId} />
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  widgets: workspaceWidgetsSelector,
});

InfoWidget.propTypes = {
  workspaceId: PropTypes.string.isRequired,
  widgets: PropTypes.arrayOf(
    PropTypes.shape({
      style: PropTypes.string.isRequired,
      type: PropTypes.string.isRequired,
      content: PropTypes.arrayOf(PropTypes.object).isRequired,
    })
  ).isRequired,
};

export default connect(mapStateToProps)(InfoWidget);
