import { createSelector } from 'reselect';

const plotsPropertiesSelector = (state) => state.PlotsProps;

export const plotsPropsByWorkspaceId = createSelector(
  plotsPropertiesSelector,
  (_, { workspaceId }) => workspaceId,
  (workspacesProps, workspaceId) => workspacesProps[workspaceId]
);

export const plotTraceConf = createSelector(
  plotsPropsByWorkspaceId,
  (plotsProps) => plotsProps?.chart_conf || {}
);

export const plotStreamByIdSelector = createSelector(
  plotsPropsByWorkspaceId,
  (_, { plotId }) => plotId,
  (plotsProps, plotId) => plotsProps?.byId[plotId]?.streamId
);

export const plotPeriodByIdSelector = createSelector(
  plotsPropsByWorkspaceId,
  (_, { plotId }) => plotId,
  (plotsProps, plotId) => plotsProps?.byId[plotId]?.period
);

export const plotTargetsByIdSelector = createSelector(
  plotsPropsByWorkspaceId,
  (_, { plotId }) => plotId,
  (plotsProps, plotId) => plotsProps?.byId[plotId]?.tags
);

export const plotDatasetIdByIdSelector = createSelector(
  plotsPropsByWorkspaceId,
  (_, { plotId }) => plotId,
  (plotsProps, plotId) => plotsProps?.byId[plotId]?.datasetId
);
