import api from './middleware/api';
import rootReducer from './reducer';
import thunkMiddleware from 'redux-thunk';
import { applyMiddleware, createStore } from 'redux';
import { composeWithDevTools } from 'redux-devtools-extension';
// import { createLogger } from 'redux-logger';
// import { loggerConfig } from './middleware/logger';

function initStore(initialState: object | null) {
  // const loggerMiddleware = createLogger({
  //   ...loggerConfig,
  //   logger: window.api.log,
  // });
  const enhancer = applyMiddleware(thunkMiddleware, api);

  return initialState
    ? createStore(rootReducer, initialState, composeWithDevTools(enhancer))
    : createStore(rootReducer, composeWithDevTools(enhancer));
}

export default initStore;
