import React, { useState } from 'react';
import cn from 'classnames';
import ModeBarButtons from 'plotly.js/src/components/modebar/buttons';

import { resetIconPath, switchIconPath } from './utils';

import styles from './ModeBar.module.css';

const ModeBar = ({ activeReset, graphDiv }) => {
  const [dragModeVal, setDragModeVal] = useState('zoom');

  const switchMode = (e) => {
    e.persist();
    if (dragModeVal === 'zoom') {
      setDragModeVal('zoom');
      ModeBarButtons.pan2d.click(graphDiv, e, 'dragmode', 'zoom');
    } else {
      setDragModeVal('zoom');
      ModeBarButtons.zoom2d.click(graphDiv, e, 'dragmode', 'zoom');
    }
  };

  const resetAxes = (e) => {
    e.persist();
    ModeBarButtons.resetScale2d.click(graphDiv, e, 'zoom', 'reset');
  };

  return (
    <div className={styles.Root}>
      <button
        className={cn(styles.ModeBarButton, {
          [styles.alternative]: dragModeVal === 'zoom',
        })}
        data-attr="dragmode"
        data-val={dragModeVal}
        onClick={switchMode}
      >
        <svg width="27px" height="27px" viewBox="0 0 27 27">
          <g transform="matrix(1,0,0,1,-2498,-1995)">
            <g transform="matrix(1,0,0,1,2522,2022)">
              <path d={switchIconPath} />
            </g>
          </g>
        </svg>
      </button>
      <button
        className={cn(styles.ModeBarButton, {
          [styles.disabled]: !activeReset,
        })}
        data-attr="zoom"
        data-val="reset"
        onClick={resetAxes}
      >
        <svg width="27px" height="27px" viewBox="0 0 27 27">
          <g transform="matrix(1,0,0,1,-2573,-1995)">
            <g transform="matrix(1,0,0,1,2600,2004)">
              <path d={resetIconPath} />
            </g>
          </g>
        </svg>
      </button>
    </div>
  );
};

export default ModeBar;
