import React, { useContext } from 'react';
import Streams from './Streams';
import Periods from './Periods';
import styles from './PlotBar.module.css';
import { PlotIdContext } from '../Plot';
import PropTypes from 'prop-types';
import Targets from '../Targets/Targets';

const PlotBar = ({ workspaceId }) => {
  const plotId = useContext(PlotIdContext);

  return (
    <div className={styles.Root}>
      <Streams plotId={plotId} workspaceId={workspaceId} />
      <Periods plotId={plotId} workspaceId={workspaceId} />
      <Targets plotId={plotId} workspaceId={workspaceId} />
    </div>
  );
};

PlotBar.propTypes = {
  workspaceId: PropTypes.string.isRequired,
};

export default PlotBar;
