import {
  SET_PLOT_STREAM,
  SET_PLOT_PERIOD,
  SET_PLOT_TARGETS,
} from '../constants';
import { workSpacePlotStreams } from '../WorkSpacesProps/selectors';

export const setPlotStream =
  (plotId, streamId, workspaceId) => async (dispatch, getState) => {
    const state = getState();
    const { defaultTags, datasetId, defaultPeriod } = workSpacePlotStreams(
      state,
      {
        workspaceId,
      }
    )?.byId[streamId];

    dispatch({
      type: SET_PLOT_STREAM,
      payload: {
        plotId,
        streamId,
        tags: defaultTags,
        datasetId,
        workspaceId,
        defaultPeriod,
      },
    });
  };

export const setPlotTargets = (plotId, tags, workspaceId) => ({
  type: SET_PLOT_TARGETS,
  payload: { plotId, tags, workspaceId },
});

export const setPlotPeriod = (plotId, period, workspaceId) => ({
  type: SET_PLOT_PERIOD,
  payload: { plotId, period, workspaceId },
});
