import React from "react";
import Advice from "./Advice";
import styles from "./Advices.module.css";
import "./Advices.css";
import { createStructuredSelector } from "reselect";
import { connect } from "react-redux";
import { advicesBySchemeIdSelector } from "../../../redux/WorkSpacesProps/selectors";
import PropTypes from "prop-types";

const Advices = ({ workspaceId, advicesProps }) => {
  return (
    <div className={styles.Root}>
      {Object.keys(advicesProps.byId).map((adviceId) => (
        <Advice
          key={adviceId}
          adviceProps={advicesProps.byId[adviceId]}
          workspaceId={workspaceId}
        />
      ))}
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  advicesProps: advicesBySchemeIdSelector,
});

Advices.propTypes = {
  workspaceId: PropTypes.string.isRequired,
  advicesProps: PropTypes.shape({
    byId: PropTypes.objectOf(PropTypes.object).isRequired,
  }).isRequired,
};

export default connect(mapStateToProps)(Advices);
