import appPropsReducer from './AppProps/reducer';
import finImpPropsReducer from './FinImpProps/reducer';
import datasetsPropsReducer from './DatasetsProps/reducer';
import downloadReducer from './DownloadProps/reducer';
import plotsPropsReducer from './PlotsProps/reducer';
import recPropsSelector from './RecProps/reducer';
import shutdownDialogReducer from './ShutdownDialog/reducer';
import specDialogReducer from './SpecDialog/reducer';
import workspacesPropsReducer from './WorkSpacesProps/reducer';
import qcodePropsSelector from  './QRcodeProps/reducer';
import { combineReducers } from 'redux';

const rootReducer = combineReducers({
  AppProps: appPropsReducer,
  DatasetsProps: datasetsPropsReducer,
  DownloadProps: downloadReducer,
  FinImpProps: finImpPropsReducer,
  PlotsProps: plotsPropsReducer,
  RecProps: recPropsSelector,
  ShutdownDialog: shutdownDialogReducer,
  SpecDialog: specDialogReducer,
  WorkSpacesProps: workspacesPropsReducer,
  QRcodeProps: qcodePropsSelector,
});

export default rootReducer;
