import produce from "immer";
import {
  CLOSE_REC,
  REC_ACTION_SUBMIT,
  SUCCESS,
  REQUEST_NEW_RECS,
} from "../constants";

const initialState = {
  recsAPIs: [
    {
      apiEndpoint: "/recommendations",
      updateInterval: 10000,
    },
  ],
  maxShown: 3,
  timerInterval: 60000,
  queue: [],
  values: {},
  error: null,
};

const reducer = (state = initialState, action) =>
  produce(state, (draft) => {
    const { type, payload, response } = action;

    switch (type) {
      case REQUEST_NEW_RECS + SUCCESS:
        for (let newProp of response) {
          draft.queue = draft.queue.filter((item) => item.id !== newProp.id);
        }

        draft.queue = draft.queue.concat(response);
        draft.queue.sort((a, b) => a.priority - b.priority);

        for (let rec of draft.queue) {
          draft.values[rec.id] = [rec.value];
        }
        break;
      case REC_ACTION_SUBMIT:
        draft.queue = draft.queue.filter((item) => item.id !== payload.recId);
        draft.error = null;
        break;
      // case REC_ACTION_SUBMIT + SUCCESS:
      //   draft.queue = draft.queue.filter((item) => item.id !== payload.recId);
      //   draft.error = null;
      //   break;
      // case REC_ACTION_SUBMIT + FAILURE:
      //   draft.queue = draft.queue.filter((item) => item.id !== payload.recId);
      //   draft.error = error;
      //   break;
      case CLOSE_REC:
        draft.queue = draft.queue.filter((item) => item.id !== payload.recId);
        break;
      default:
        return;
    }
  });

export default reducer;
