import { createSelector } from "reselect";

const recommendationsSelector = (state) => state.RecProps;

export const recsAPIsSelector = createSelector(
  recommendationsSelector,
  (recs) => recs?.recsAPIs || []
);

export const recsQueueSelector = createSelector(
  recommendationsSelector,
  (recs) => recs?.queue || []
);

export const recUnstableTextSelector = createSelector(
  recommendationsSelector,
  (recs) => recs?.unstableText || " ~ "
);

export const recsValuesSelector = createSelector(
  recommendationsSelector,
  (recs) => recs?.values || {}
);

export const maxShownRecsSelector = createSelector(
  recommendationsSelector,
  (recs) => recs?.maxShown || 3
);

export const recsTimerIntervalSelector = createSelector(
  recommendationsSelector,
  (recs) => recs?.timerInterval || 60000
);
