import produce from "immer";
import {
  CLOSE_QRCODE_DIALOG,
  OPEN_QRCODE_DIALOG,
  SUCCESS,
  REQUEST_QRCODE,
} from "../constants";

const initialState = {
  src: "images/qr_code.png",
  isOpened: false,
};

const reducer = (state = initialState, action) =>
  produce(state, (draft) => {
    const { type, response } = action;

    switch (type) {
      case REQUEST_QRCODE + SUCCESS:

        draft.src = response.src;
        break;
      case OPEN_QRCODE_DIALOG:
        draft.isOpened = true;
        break;

      case CLOSE_QRCODE_DIALOG:
        draft.isOpened = false;
        break;
      default:
        return;
    }
  });

export default reducer;
