import produce from "immer";
import {
  SET_PLOT_STREAM,
  SET_PLOT_PERIOD,
  SET_PLOT_TARGETS,
} from "../constants";

const initialState = {
  default: {
    byId: {
      left: {
        streamId: "default",
        tags: ["", "", "", ""],
        period: 1440,
        datasetId: "default",
      },
      right: {
        streamId: "default",
        tags: ["", "", "", ""],
        period: 1440,
        datasetId: "default",
      },
    },
  },
};

const reducer = (state = initialState, action) =>
  produce(state, (draft) => {
    const { type, payload } = action;

    switch (type) {
      case SET_PLOT_STREAM:
        draft[payload.workspaceId].byId[payload.plotId].streamId =
          payload.streamId;
        draft[payload.workspaceId].byId[payload.plotId].tags = payload.tags;
        draft[payload.workspaceId].byId[payload.plotId].datasetId =
          payload.datasetId;
        if (
          draft[payload.workspaceId].byId[payload.plotId].datasetId !==
          payload.datasetId
        ) {
          draft[payload.workspaceId].byId[payload.plotId].period =
            payload.defaultPeriod;
        }
        break;
      case SET_PLOT_PERIOD:
        draft[payload.workspaceId].byId[payload.plotId].period = payload.period;
        break;
      case SET_PLOT_TARGETS:
        if (payload.tags.length >= 5) {
          break;
        } else {
          draft[payload.workspaceId].byId[payload.plotId].tags = payload.tags;
          break;
        }
      default:
        return;
    }
  });

export default reducer;
