import React from "react";

import Period from "./Period";

import styles from "./Periods.module.css";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { createStructuredSelector } from "reselect";
import { plotDatasetIdByIdSelector } from "../../../../../../redux/PlotsProps/selectors";
import { workspaceDatasetsPropsSelector } from "../../../../../../redux/DatasetsProps/selectors";

const Periods = ({ plotId, workspaceId, datasetId, datasetsProps }) => {
  return (
    <div className={styles.Root}>
      {datasetsProps &&
        datasetId &&
        datasetsProps[datasetId].periods.map((periodProps) => (
          <Period
            key={periodProps.text}
            buttonProps={periodProps}
            plotId={plotId}
            workspaceId={workspaceId}
          />
        ))}
    </div>
  );
};

Periods.propTypes = {
  plotId: PropTypes.string.isRequired,
  workspaceId: PropTypes.string.isRequired,
  datasetId: PropTypes.string.isRequired,
  datasetsProps: PropTypes.objectOf(
    PropTypes.shape({
      periods: PropTypes.arrayOf(
        PropTypes.shape({
          text: PropTypes.string.isRequired,
          value: PropTypes.number.isRequired,
        })
      ).isRequired,
    })
  ).isRequired,
};

const mapStateToProps = createStructuredSelector({
  datasetId: plotDatasetIdByIdSelector,
  datasetsProps: workspaceDatasetsPropsSelector,
});

export default connect(mapStateToProps)(Periods);
