export function getLastPoint(dataset, targetId, precision) {
  try {
    const value = Number(Object.values(dataset[targetId]).slice(-1)[0]);

    let result = value.toFixed(precision);
    result = result.length >= 7 ? result.slice(0, 7) : result;

    return Number(result).toLocaleString();
  } catch (err) {
    return " ~ ";
  }
}

export function getStatus(dataset, targetId) {
  try {
    return Number(Object.values(dataset[targetId]).slice(-1)[0]);
  } catch (err) {
    return true;
  }
}
