import React from "react";
import Recommendation from "./Recommendation";
import styles from "./Recommendations.module.css";
import "./Recommendations.css";
import { createStructuredSelector } from "reselect";
import { connect, useSelector } from "react-redux";
import { recsQueueSelector, recUnstableTextSelector } from "../../../redux/RecProps/selectors";
import { recActionSubmit } from "../../../redux/RecProps/actions";
import { CSSTransition, TransitionGroup } from "react-transition-group";
import PropTypes from "prop-types";
import { appWorkspace } from "../../../redux/AppProps/selectors";
import { getWorkspaceStatus } from "./utils";
import { workSpaceStatusTagSelector } from "../../../redux/WorkSpacesProps/selectors";
import { workspaceDatasetsPropsSelector } from "../../../redux/DatasetsProps/selectors";

const Recommendations = ({ recQueue, submitRecAction, currWorkspace, unstableText }) => {
  const unstableBannerProps = {
    id: "unstable",
    priority: 1,
    datetime: "00:00:00",
    text: unstableText,
  };

  const statusTag = useSelector((state) => workSpaceStatusTagSelector(state, { workspaceId: currWorkspace }));
  const data = useSelector((state) => workspaceDatasetsPropsSelector(state, { workspaceId: currWorkspace }));
  const workspaceStatus = getWorkspaceStatus(data?.minutesDataset.dataset, statusTag);
  const slots = !workspaceStatus ? [unstableBannerProps] : recQueue.slice(0, 3);

  return (
    <div className={styles.Root}>
      <h1 className={styles.Title}></h1>
      <TransitionGroup>
        {slots.map((recProps) => (
          <CSSTransition key={recProps.id} timeout={400} classNames="rec-animation">
            <Recommendation key={recProps.id} props={{ ...recProps }} actionHandler={submitRecAction} />
          </CSSTransition>
        ))}
      </TransitionGroup>
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  recQueue: recsQueueSelector,
  currWorkspace: appWorkspace,
  unstableText: recUnstableTextSelector,
});

const mapDispatchToProps = (dispatch) => ({
  submitRecAction: (action, recId) => dispatch(recActionSubmit(action, recId)),
});

Recommendations.propTypes = {
  recQueue: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string.isRequired,
    }).isRequired
  ),
  submitRecAction: PropTypes.func.isRequired,
  currWorkspace: PropTypes.string.isRequired,
  unstableText: PropTypes.string.isRequired,
};

export default connect(mapStateToProps, mapDispatchToProps)(Recommendations);
