import React from "react";
import styles from "./SpecDialog.module.css";
import cn from "classnames";
import RegimeButton from "./RegimeButton";
import SpecInput from "./SpecInput";
import { FormControl, FormControlLabel, RadioGroup } from "@material-ui/core";
import WhiteRadio from "../RadioBtnsGroup/RadioButtons";
import PropTypes from "prop-types";
import {useTranslation} from "react-i18next";

const safeChecker = (values) => {
  const type = typeof values;
  if (values == null || type !== "object") return false;

  for (const key of Object.keys(values)) {
    if (!values[key]) return false;
  }

  return true;
};

export function SaveBtn({ saveHandler, closeHandler, currConsts }) {
  const isSafe = safeChecker(currConsts);
  const { t } = useTranslation();
  return (
    <div className={styles.Buttons}>
      <button
        onClick={isSafe ? saveHandler : () => {}}
        className={cn(styles.WhiteButton, { [styles.DisabledBtn]: !isSafe })}
      >
        {t(`Dialogs.SetSpecDialog.acceptBtn`)}
      </button>
      <button onClick={closeHandler} className={styles.WhiteButton}>
        {t(`Dialogs.SetSpecDialog.cancelBtn`)}
      </button>
    </div>
  );
}

SaveBtn.propTypes = {
  saveHandler: PropTypes.func.isRequired,
  closeHandler: PropTypes.func.isRequired,
  currConsts: PropTypes.objectOf(
    PropTypes.oneOfType([PropTypes.string, PropTypes.number])
  ),
};

export function DefaultContent({
  workspaceId,
  currSpec,
  currConsts,
  specButtons,
  constButtons,
  saveHandler,
  closeHandler,
}) {
  const { t } = useTranslation();
  return (
    <div
      className={cn(styles.Content, {
        [styles.Default]: true,
      })}
    >
      <h1 className={styles.Title}>{t(`Dialogs.SetSpecDialog.title`)}</h1>
      <div className={styles.DefaultSpecsContainer}>
        {constButtons.map((btnProps) => (
          <SpecInput
            key={btnProps.label}
            spec={currConsts[btnProps.value]}
            text={btnProps.label}
            postfix={btnProps.postfix}
            workspaceId={workspaceId}
            currSpecs={currConsts}
            tagId={btnProps.value}
            validation={btnProps.validation}
            step={btnProps.step}
          />
        ))}
      </div>
      <SaveBtn
        saveHandler={saveHandler}
        closeHandler={closeHandler}
        currConsts={currConsts}
      />
    </div>
  );
}

DefaultContent.propTypes = {
  workspaceId: PropTypes.string.isRequired,
  currSpec: PropTypes.string.isRequired,
  currConsts: PropTypes.object.isRequired,
  specButtons: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string.isRequired,
      value: PropTypes.string.isRequired,
    })
  ).isRequired,
  constButtons: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string.isRequired,
      value: PropTypes.string.isRequired,
      validation: PropTypes.shape({
        inputType: PropTypes.oneOf(["number", "text"]),
      }).isRequired,
    })
  ).isRequired,
  saveHandler: PropTypes.func.isRequired,
  closeHandler: PropTypes.func.isRequired,
};

export function CompactContent({
  workspaceId,
  currSpec,
  currConsts,
  specButtons,
  constButtons,
  setNewSpec,
  updateConsts,
  saveHandler,
  closeHandler,
}) {
  const { t } = useTranslation();
  return (
    <div
      className={cn(styles.Content, {
        [styles.Compact]: true,
      })}
    >
      <h1 className={styles.Title}>{t(`Dialogs.SetSpecDialog.title`)}</h1>
      <div className={styles.CompactSpecsContainer}>
        {constButtons.map((btnProps) => (
          <SpecInput
            key={btnProps.value}
            spec={currConsts[btnProps.value]}
            text={btnProps.label}
            postfix={btnProps.postfix}
            workspaceId={workspaceId}
            currSpecs={currConsts}
            tagId={btnProps.value}
            validation={btnProps.validation}
            step={btnProps.step}
          />
        ))}
      </div>
      <SaveBtn
        saveHandler={saveHandler}
        closeHandler={closeHandler}
        currConsts={currConsts}
      />
    </div>
  );
}

CompactContent.propTypes = {
  workspaceId: PropTypes.string.isRequired,
  currSpec: PropTypes.string.isRequired,
  currConsts: PropTypes.objectOf(
    PropTypes.oneOfType([PropTypes.string, PropTypes.number])
  ).isRequired,
  specButtons: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string.isRequired,
      value: PropTypes.string.isRequired,
    })
  ).isRequired,
  constButtons: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string.isRequired,
      value: PropTypes.string.isRequired,
      validation: PropTypes.shape({
        inputType: PropTypes.oneOf(["number", "text"]),
      }).isRequired,
    })
  ).isRequired,
  setNewSpec: PropTypes.func.isRequired,
  updateConsts: PropTypes.func.isRequired,
  saveHandler: PropTypes.func.isRequired,
  closeHandler: PropTypes.func.isRequired,
};
