import produce from "immer";
import {
  CLOSE_SPEC_DIALOG,
  FAILURE,
  OPEN_SPEC_DIALOG,
  SAVE_SPECS_AND_CONSTS,
  SET_CONSTANTS,
  SET_SPECIFICATION,
  SUCCESS,
  UPDATE_CONSTANTS,
} from "../constants";

const initialState = {
  isOpened: false,
  workspaceId: null,
  datasetId: "default",
  apiEndpoints: {
    set: "/set_specification_and_consts?",
    get: "/current_specification_and_consts",
    update: "/current_consts?",
    datasetUpdate: "/calculated_parameters",
  },
  error: null,
  specification: null,
  constants: {},
};

const validateConstants = (consts, defaults) => {
  try {
    const validKeys = Object.keys(defaults);
    const responseKeys = Object.keys(consts);
    const result = { ...consts };

    for (const key of validKeys) {
      if (responseKeys.includes(key)) {
        continue;
      } else {
        result[key] = defaults[key];
      }
    }

    return result;
  } catch (err) {
    return { ...defaults };
  }
};

const reducer = (state = initialState, action) =>
  produce(state, (draft) => {
    const { type, payload, response, error } = action;

    switch (type) {
      case OPEN_SPEC_DIALOG + SUCCESS:
        const specification = Object.keys(response)[0];
        const constants = response[specification];

        draft.isOpened = true;
        draft.workspaceId = payload.workspaceId;
        draft.specification = specification;
        draft.constants = validateConstants(constants, payload.default.constants);
        draft.error = null;
        break;
      case OPEN_SPEC_DIALOG + FAILURE:
        draft.isOpened = true;
        draft.workspaceId = payload.workspaceId;
        draft.specification = payload.default.specification;
        draft.constants = payload.default.constants;
        draft.error = error;
        break;
      case SET_CONSTANTS:
        draft.constants = payload.constants;
        break;
      case UPDATE_CONSTANTS + SUCCESS:
        draft.constants = validateConstants(response, payload.default.constants);
        break;
      case UPDATE_CONSTANTS + FAILURE:
        draft.constants = payload.default.constants;
        break;
      case SET_SPECIFICATION:
        draft.specification = payload.specification;
        break;
      case CLOSE_SPEC_DIALOG:
        draft.isOpened = false;
        draft.workspaceId = null;
        break;
      case SAVE_SPECS_AND_CONSTS + SUCCESS:
        draft.isOpened = false;
        draft.workspaceId = null;
        break;
      case SAVE_SPECS_AND_CONSTS + FAILURE:
        draft.isOpened = false;
        draft.workspaceId = null;
        break;
      default:
        return;
    }
  });

export default reducer;
