import {
  CLOSE_SPEC_DIALOG,
  LOAD_DATASET,
  OPEN_SPEC_DIALOG,
  SAVE_SPECS_AND_CONSTS,
  SET_CONSTANTS,
  SET_SPECIFICATION,
  UPDATE_CONSTANTS,
} from "../constants";
import { objToURLParams } from "../utils";
import { workspaceSpecDefaultsSelector } from "../WorkSpacesProps/selectors";

export const openSpecDialog = (workspaceId) => async (dispatch, getState) => {
  const state = getState();
  const apiEndpoints = state.SpecDialog.apiEndpoints;
  const defaults = workspaceSpecDefaultsSelector(state, { workspaceId });

  dispatch({
    type: OPEN_SPEC_DIALOG,
    payload: { workspaceId, default: { ...defaults } },
    CallAPI: `${workspaceId}${apiEndpoints.get}`,
  });
};

export const updateSpecInputs =
  (workspaceId, specification) => async (dispatch, getState) => {
    const state = getState();
    const apiEndpoints = state.SpecDialog.apiEndpoints;
    const params = objToURLParams({ specification });
    const defaults = workspaceSpecDefaultsSelector(state, { workspaceId });

    dispatch({
      type: UPDATE_CONSTANTS,
      payload: { workspaceId, default: { ...defaults } },
      CallAPI: `${workspaceId}${apiEndpoints.update}${params}`,
    });
  };

export const closeSpecDialog = () => ({
  type: CLOSE_SPEC_DIALOG,
  payload: {},
});

export const setSpecification = (specification) => ({
  type: SET_SPECIFICATION,
  payload: { specification },
});

export const setConstants = (constants) => ({
  type: SET_CONSTANTS,
  payload: { constants },
});

export const saveSpecifications = () => async (dispatch, getState) => {
  const state = getState();
  const { specification, constants, workspaceId, apiEndpoints, datasetId } =
    state.SpecDialog;
  const params = objToURLParams({
    specification,
    ...constants,
  });

  dispatch({
    type: SAVE_SPECS_AND_CONSTS,
    payload: {},
    CallAPI: `${workspaceId}${apiEndpoints.set}${params}`,
  });
  dispatch({
    type: LOAD_DATASET,
    payload: { workspaceId, datasetId },
    CallAPI: `${workspaceId}${apiEndpoints.datasetUpdate}`,
  });
};
