import "./i18n/config";
import "./index.css";
import App from "./components/App";
import React from "react";
import ReactDOM from "react-dom";
import initStore from "./redux/store";
import { Provider } from "react-redux";
import { requestAppSettings } from "./utils";

/**
 * Assign console.log to electron-log. To store .log files and
 * bind logs with redux-logger.
 */
// console.log = Object.assign(console, window.api.log.functions);

/**
 * External libs added through public/preload.js.
 * @type api -> Use this to add external libs.
 * TODO: define type of log object
 */
declare global {
  interface Window {
    store: any;
    api: {
      send: (channel: string, data: any) => void;
      receive: (channel: string, handler: (data: any) => object | void) => void;
      log: any;
    };
    endpoint: string;
  }
}

function renderApp(store: any, globalStore = false) {
  //! Dev only.
  //! In some cases it's useful to have global store (first prod run tests).
  if (globalStore) window.store = store;

  ReactDOM.render(
    <Provider store={store}>
      <App />
    </Provider>,
    document.getElementById("root")
  );
}

requestAppSettings()
  .then((settings) => renderApp(initStore(settings), true))
  .catch(() => renderApp(initStore(null), true));
