import React from 'react';

import FrameBarButton from './FrameBarButton';
import ExitIcon from '../../assets/images/FrameBar_Exit.png';
import CollapseIcon from '../../assets/images/FrameBar_Collapse.png';

import styles from './FrameBar.module.css';
import ShutdownDialog from './ShutdownDialog';
import { openShutdownDialog } from '../../redux/ShutdownDialog/actions';
import { connect } from 'react-redux';

const FrameBar = ({ openDialog }) => {
  const appExit = () => {
    window?.api?.send?.('appExit', true);
  };

  const appCollapse = () => {
    window?.api?.send?.('appCollapse', true);
  };

  return (
    <>
      <div className={styles.Root}>
        <FrameBarButton
          iconPath={ExitIcon}
          marginTop={50}
          marginRight={24}
          onClick={openDialog}
        />
        <FrameBarButton
          iconPath={CollapseIcon}
          marginTop={62}
          marginRight={0}
          onClick={appCollapse}
        />
      </div>
      <ShutdownDialog appExit={appExit} />
    </>
  );
};

const mapDispatchToProps = (dispatch) => ({
  openDialog: () => dispatch(openShutdownDialog()),
});

export default connect(null, mapDispatchToProps)(FrameBar);
