import produce from 'immer';
import { OPEN_SHUTDOWN_DIALOG, CLOSE_SHUTDOWN_DIALOG } from '../constants';

const initialState = {
  isOpened: false,
};

const reducer = (state = initialState, action) =>
  produce(state, (draft) => {
    const { type } = action;

    switch (type) {
      case OPEN_SHUTDOWN_DIALOG:
        draft.isOpened = true;
        break;
      case CLOSE_SHUTDOWN_DIALOG:
        draft.isOpened = false;
        break;
      default:
        break;
    }
  });

export default reducer;
