import React from 'react';
import PropTypes from 'prop-types';
import WhiteRadio from './RadioButtons';
import { FormControl, FormControlLabel, RadioGroup } from '@material-ui/core';
import styles from './RadioBtnsGroup.module.css';

function RadioBtnsGroup({ storeValues, props, handler }) {
  const handleChange = (ev) => {
    const value = ev.target.value;
    handler(value, props.storeProp);
  };

  const storeValue = storeValues[props.storeProp];

  return (
    <div className={styles.Root}>
      <FormControl component="fieldset">
        <h1 className={styles.FormLabel}>{props.title}</h1>
        <RadioGroup
          aria-label="props.title"
          name="props.title"
          value={storeValue}
          onChange={handleChange}
        >
          {props.buttons.map((btnProp, index) => (
            <FormControlLabel
              key={`${btnProp.label}${index}`}
              classes={{ label: styles.RadioLabel }}
              style={{
                color: storeValue === btnProp.value ? 'white' : 'gray',
              }}
              value={btnProp.value}
              control={<WhiteRadio />}
              label={btnProp.label}
            />
          ))}
        </RadioGroup>
      </FormControl>
    </div>
  );
}

RadioBtnsGroup.propTypes = {
  storeValues: PropTypes.object.isRequired,
  handler: PropTypes.func.isRequired,
  props: PropTypes.shape({
    title: PropTypes.string.isRequired,
    storeProp: PropTypes.string.isRequired,
    buttons: PropTypes.arrayOf(
      PropTypes.shape({
        style: PropTypes.string.isRequired,
        value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
        label: PropTypes.string.isRequired,
      })
    ).isRequired,
  }).isRequired,
};

export default RadioBtnsGroup;
