import React from 'react';

import styles from './FrameBarButton.module.css';

const FrameBarButton = ({ iconPath, marginTop, marginRight, onClick }) => {
  return (
    <div
      onClick={onClick}
      className={styles.Root}
      style={{ marginRight: `${marginRight}px` }}
    >
      <img
        src={iconPath}
        alt={''}
        className={styles.Icon}
        style={{ top: `${marginTop}%` }}
      />
    </div>
  );
};

export default FrameBarButton;