import produce from "immer";

import { FAILURE, LOAD_DATASET, SUCCESS } from "../constants";

const initialState = {
  default: {
    byId: {
      default: {
        id: "default",
        apiEndpoints: {
          get: "/calculated_parameters",
          update: "/basic_functionality",
        },
        updateInterval: 60000,
        dataset: {},
        periods: [{ text: "default", value: 1440 }],
        error: null,
      },
    },
  },
};

const reducer = (state = initialState, action) =>
  produce(state, (draft) => {
    const { type, payload, response, error } = action;

    switch (type) {
      case LOAD_DATASET + SUCCESS:
        draft[payload.workspaceId].byId[payload.datasetId].dataset = response;
        draft[payload.workspaceId].byId[payload.datasetId].error = null;
        break;
      case LOAD_DATASET + FAILURE:
        draft[payload.workspaceId].byId[payload.datasetId].error = error;
        break;
      default:
        return;
    }
  });

export default reducer;
