/**
 * Preload datasets before app show and
 * starts update intervals for all datasets.
 * @param workspaces -> arrayOf(string)
 * @param datasetsProps -> objectOf(described in App.jsx)
 * @param calculator -> func(request sender for calculating new data)
 * @param updater -> func(redux action function)
 * @returns {Promise<void>}
 */
export async function appDatasetsUpdater(
  workspaces,
  datasetsProps,
  calculator,
  updater
) {
  for (const workspaceId of workspaces) {
    const { byId } = datasetsProps[workspaceId];
    await updateWorkspaceDatasets(
      workspaceId,
      Object.keys(byId),
      byId,
      updater,
      calculator
    );
  }
}

async function updateWorkspaceDatasets(
  workspaceId,
  datasetsIds,
  datasetsPropsById,
  updater,
  calculator
) {
  for (const datasetId of datasetsIds) {
    const datasetProps = datasetsPropsById[datasetId];

    await updateDataset(workspaceId, datasetProps, updater, calculator).then(
      () => {
        setInterval(
          () => updateDataset(workspaceId, datasetProps, updater, calculator),
          datasetProps.updateInterval
        );
      }
    );
  }
}

async function updateDataset(workspaceId, datasetProps, updater, calculator) {
  if (datasetProps.apiEndpoints.update)
    await calculator(workspaceId, datasetProps.apiEndpoints.update);
  updater(workspaceId, {
    id: datasetProps.id,
    endpoint: datasetProps.apiEndpoints.get,
  });
}

/**
 * Preload recommendation before app show and
 * starts update intervals for all recs.
 * @param recProps -> arrayOf(described in App.jsx)
 * @param updater -> func(redux action)
 * @returns {Promise<void>}
 */
export async function appRecommendationsUpdater(recProps, updater) {
  for (const rec of recProps) {
    updater(rec.workspaceId, rec.apiEndpoint);
    setInterval(() => {
      updater(rec.workspaceId, rec.apiEndpoint);
    }, rec.updateInterval);
  }
}
