import React from "react";
import cn from "classnames";
import styles from "../SpecDialog/SpecDialog.module.css";
import { createStructuredSelector } from "reselect";
import {
  closeSpecDialog,
  saveSpecifications,
  setSpecification,
  updateSpecInputs,
} from "../../../../redux/SpecDialog/actions";
import { connect } from "react-redux";
import {
  specDialogBtnGroupsSelector,
  specDialogContentStyleSelector,
  specDialogProps,
} from "../../../../redux/SpecDialog/selectors";
import { loadDataset } from "../../../../redux/DatasetsProps/actions";
import { CompactContent, DefaultContent } from "./StyledContent";
import PropTypes from "prop-types";

const SpecDialog = ({
  dialogProps,
  contentStyle,
  buttonGroups,
  closeDialog,
  saveSpecs,
  setNewRegime,
  updateSpecs,
}) => {
  const { workspaceId, isOpened, specification, constants } = dialogProps;

  if (workspaceId == null) {
    return (
      <div className={cn(styles.Root, { [styles.Opened]: isOpened })}>
        <div className={styles.Blocker} onClick={closeDialog} />
      </div>
    );
  }

  const saveHandler = () => {
    saveSpecs();
  };
  const content = () => {
    switch (contentStyle) {
      case "default":
        return (
          <DefaultContent
            workspaceId={workspaceId}
            currSpec={specification}
            currConsts={constants}
            specButtons={buttonGroups["specButtons"]}
            constButtons={buttonGroups["constButtons"]}
            saveHandler={saveHandler}
            closeHandler={closeDialog}
          />
        );
      case "compact":
        return (
          <CompactContent
            workspaceId={workspaceId}
            currSpec={specification}
            currConsts={constants}
            specButtons={buttonGroups["specButtons"]}
            constButtons={buttonGroups["constButtons"]}
            setNewSpec={setNewRegime}
            updateConsts={updateSpecs}
            saveHandler={saveHandler}
            closeHandler={closeDialog}
          />
        );
      default:
        return null;
    }
  };

  return (
    <div className={cn(styles.Root, { [styles.Opened]: isOpened })}>
      <div className={styles.Blocker} onClick={closeDialog} />
      {content()}
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  dialogProps: specDialogProps,
  contentStyle: specDialogContentStyleSelector,
  buttonGroups: specDialogBtnGroupsSelector,
});

const mapDispatchToProps = (dispatch) => ({
  closeDialog: () => dispatch(closeSpecDialog()),
  saveSpecs: () => dispatch(saveSpecifications()),
  loadDataset: (id) => dispatch(loadDataset(id)),
  setNewRegime: (regimeId) => dispatch(setSpecification(regimeId)),
  updateSpecs: (id, regime) => dispatch(updateSpecInputs(id, regime)),
});

SpecDialog.propTypes = {
  dialogProps: PropTypes.shape({
    workspaceId: PropTypes.string,
    isOpened: PropTypes.bool.isRequired,
    specification: PropTypes.string,
    constants: PropTypes.objectOf(
      PropTypes.oneOfType([PropTypes.string, PropTypes.number])
    ).isRequired,
  }).isRequired,
  contentStyle: PropTypes.string.isRequired,
  buttonGroups: PropTypes.object,
  closeDialog: PropTypes.func.isRequired,
  saveSpecs: PropTypes.func.isRequired,
  setNewRegime: PropTypes.func.isRequired,
  updateSpecs: PropTypes.func.isRequired,
};

export default connect(mapStateToProps, mapDispatchToProps)(SpecDialog);
