import React from 'react';
import PlotBar from './PlotBar';
import styles from './Plot.module.css';
import './Plot.css';
import Chart from './Chart/Chart';
import { createStructuredSelector } from 'reselect';
import { connect } from 'react-redux';
import { appWorkspace } from '../../../../redux/AppProps/selectors';
import { CSSTransition, SwitchTransition } from 'react-transition-group';
import PropTypes from 'prop-types';

export const PlotIdContext = React.createContext('');

const Plot = ({ id, workspaceId }) => {
  return (
    <PlotIdContext.Provider value={id}>
      <SwitchTransition>
        <CSSTransition
          key={workspaceId}
          addEndListener={(node, done) =>
            node.addEventListener('transitionend', done, false)
          }
          classNames="plot-fade"
        >
          <div className={styles.Root}>
            <PlotBar workspaceId={workspaceId} />
            <Chart plotId={id} workspaceId={workspaceId} />
          </div>
        </CSSTransition>
      </SwitchTransition>
    </PlotIdContext.Provider>
  );
};

const mapStateToProps = createStructuredSelector({
  workspaceId: appWorkspace,
});

Plot.propTypes = {
  id: PropTypes.string.isRequired,
  workspaceId: PropTypes.string.isRequired,
};

export default connect(mapStateToProps)(Plot);
