import React from "react";
import styles from "./Scheme.module.css";
import "./Scheme.css";
import Slider from "infinite-react-carousel";
import { connect } from "react-redux";
import { createStructuredSelector } from "reselect";
import { workSpaceSchemes } from "../../../redux/WorkSpacesProps/selectors";
import PropTypes from "prop-types";
import Advices from "../Advices";
import { CSSTransition, SwitchTransition } from "react-transition-group";
import Labels from "../Labels";

const sliderSettings = {
  arrows: false,
  arrowsBlock: false,
  dots: true,
  overScan: 1,
  swipe: true,
};

// TODO: Add adaptive scheme switcher by data tag

const Scheme = ({ workspaceId, schemesProps }) => {
  return (
    <SwitchTransition>
      <CSSTransition
        key={workspaceId}
        addEndListener={(node, done) =>
          node.addEventListener("transitionend", done, false)
        }
        classNames="scheme-fade"
      >
        <div className={styles.Root}>
          <Slider {...sliderSettings} workspaceId={workspaceId}>
            {schemesProps &&
              Object.keys(schemesProps.byId).map((schemeId) => (
                <div key={schemeId}>
                  <Advices schemeId={schemeId} workspaceId={workspaceId} />
                  <img
                    src={'http://45.61.48.18/' + schemesProps.byId[schemeId].src}
                    alt={""}
                  />
                  <Labels schemeId={schemeId} workspaceId={workspaceId} />
                </div>
              ))}
          </Slider>
        </div>
      </CSSTransition>
    </SwitchTransition>
  );
};

const mapStateToProps = createStructuredSelector({
  schemesProps: workSpaceSchemes,
});

Scheme.propTypes = {
  workspaceId: PropTypes.string.isRequired,
  schemesProps: PropTypes.shape({
    byId: PropTypes.objectOf(
      PropTypes.shape({
        src: PropTypes.string.isRequired,
        size: PropTypes.shape({
          width: PropTypes.number,
          height: PropTypes.number,
        }),
      })
    ).isRequired,
  }),
};

export default connect(mapStateToProps)(Scheme);
