import React from 'react';
import cn from 'classnames';
import StreamCheckmark from '../../../../../../../assets/images/Stream_Checkmark.png';
import styles from './Stream.module.css';
import { connect } from 'react-redux';
import { setPlotStream } from '../../../../../../../redux/PlotsProps/actions';
import PropTypes from 'prop-types';

const Stream = ({ streamProps, closeDropdown, isSelected, setStream }) => {
  const handleClick = () => {
    closeDropdown();
    setStream(streamProps.id);
  };

  return (
    <li className={styles.Root} onClick={handleClick}>
      <span className={styles.Text}>{streamProps.text}</span>
      <img
        src={StreamCheckmark}
        alt={''}
        className={cn(styles.Checkmark, {
          [styles.Checked]: isSelected,
        })}
      />
    </li>
  );
};

const mapDispatchToProps = (dispatch, ownProps) => ({
  setStream: (streamId) =>
    dispatch(setPlotStream(ownProps.plotId, streamId, ownProps.workspaceId)),
});

Stream.propTypes = {
  streamProps: PropTypes.shape({
    id: PropTypes.string.isRequired,
    text: PropTypes.string.isRequired,
  }).isRequired,
  isSelected: PropTypes.bool.isRequired,
  setStream: PropTypes.func.isRequired,
};

export default connect(null, mapDispatchToProps)(Stream);
