import React from 'react';
import cn from 'classnames';

import DownloadIcon from '../../../../../assets/images/WidgetSettings_Download.png';
import SpecificationIcon from '../../../../../assets/images/WidgetSettings_Specification.png';

import styles from './SettingsBar.module.css';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { appWorkspace } from '../../../../../redux/AppProps/selectors';
import { setAppWorkspace } from '../../../../../redux/AppProps/actions';
import { openDownloadDialog } from '../../../../../redux/DownloadProps/actions';
import { openSpecDialog } from '../../../../../redux/SpecDialog/actions';
import {
  workspaceDialogPropsSelector,
  workSpaceLabelSelector, workspacesIdsSelector
} from "../../../../../redux/WorkSpacesProps/selectors";
import PropTypes from 'prop-types';

const Button = ({ src, handler }) => {
  return (
    <button className={styles.IconButton} onClick={handler}>
      <img src={src} alt={''} />
    </button>
  );
};

const SettingsBar = ({
  workspaceId,
  selectedWorkspace,
  allWorkspaces,
  workSpaceLabel,
  dialogsProps,
  setWorkspace,
  openDownload,
  openSpecs,
}) => {
  return (
    <div className={styles.Root}>
      <button
        onClick={setWorkspace}
        className={cn(styles.Name, {
          [styles.Checked]: workspaceId === selectedWorkspace,
          [styles.Single]: allWorkspaces.length <= 1,
        })}
      >
        {workSpaceLabel}
      </button>
      {dialogsProps.dialogs.map((dialogId) => {
        switch (dialogId) {
          case 'specDialog':
            return (
              <Button
                key={dialogId}
                src={SpecificationIcon}
                handler={openSpecs}
              />
            );
          case 'downloadDialog':
            return (
              <Button
                key={dialogId}
                src={DownloadIcon}
                handler={openDownload}
              />
            );
          default:
            return null;
        }
      })}
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  selectedWorkspace: appWorkspace,
  allWorkspaces: workspacesIdsSelector,
  workSpaceLabel: workSpaceLabelSelector,
  dialogsProps: workspaceDialogPropsSelector,
});

const mapDispatchToProps = (dispatch, ownProps) => ({
  setWorkspace: () => dispatch(setAppWorkspace(ownProps.workspaceId)),
  openDownload: () => dispatch(openDownloadDialog(ownProps.workspaceId)),
  openSpecs: () => dispatch(openSpecDialog(ownProps.workspaceId)),
});

SettingsBar.propTypes = {
  workspaceId: PropTypes.string.isRequired,
  selectedWorkspace: PropTypes.string.isRequired,
  workSpaceLabel: PropTypes.string.isRequired,
  dialogsProps: PropTypes.shape({
    dialogs: PropTypes.arrayOf(PropTypes.string).isRequired,
  }),
  setWorkspace: PropTypes.func.isRequired,
  openDownload: PropTypes.func.isRequired,
  openSpecs: PropTypes.func.isRequired,
};

export default connect(mapStateToProps, mapDispatchToProps)(SettingsBar);
