import { createSelector } from "reselect";

const qrcodeSelector = (state) => state.QRcodeProps;

export const isQRcodeOpened = createSelector(
  qrcodeSelector,
  (props) => props.isOpened
);


export const qrcodeTimerIntervalSelector = createSelector(
  qrcodeSelector,
  (qrcodes) => qrcodes?.timerInterval || 60000
);


export const workSpaceQRcode = createSelector(
  qrcodeSelector,
  (qrcodes) => qrcodes?.src
);
