export const requestCalculation = async (id, api) => {
  const route = `${id}${api}`;

  await fetch('http://45.61.48.18/' + route, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: "{}",
  }).catch((err) => {
    window?.api?.log?.error("\nRequest dataset calculation error!");
    window?.api?.log?.error(err.message);
  });
};

export const requestAppSettings = async () => {
  const route = "aio_ui/config";
  return await fetch('http://45.61.48.18/' + route)
    .then((response) => {
      return response.json();
    })
    .catch((err) => {
      window?.api?.log?.error("\nRequest app settings error!");
      window?.api?.log?.error(err.message);
    });
};
