export const prepareValue = (dataset, targetId, precision, texted) => {
  try {
    let value = Number(Object.values(dataset[targetId]).slice(-1)[0]);

    if (texted) return texted[`${value}`];

    if (value <= 0) return Number(0).toFixed(0);

    value = value.toFixed(precision);

    return Number(value).toLocaleString();
  } catch (err) {
    if (targetId == null) return "";
    return " ~ ";
  }
};

export const prepareMargin = (name) => {
  const len = name.length;
  if (!len) return 0;
  if (len < 3) return 82 - len * 23;
  if (len >= 3) return 13;
};

export function getStatus(dataset, targetId) {
  try {
    return Number(Object.values(dataset[targetId]).slice(-1)[0]);
  } catch (err) {
    return true;
  }
}
