import React from 'react';

import InfoWidget from './InfoWidget';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { workspacesIdsSelector } from '../../../redux/WorkSpacesProps/selectors';
import PropTypes from 'prop-types';

const InfoWidgets = ({ workspaces }) => {
  return (
    <>
      {workspaces.map((item) => (
        <InfoWidget key={item} workspaceId={item} />
      ))}
    </>
  );
};

const mapStateToProps = createStructuredSelector({
  workspaces: workspacesIdsSelector,
});

InfoWidgets.prpTypes = {
  workspaces: PropTypes.arrayOf(PropTypes.string).isRequired,
};

export default connect(mapStateToProps)(InfoWidgets);
