import React, { useEffect, useState } from "react";
import cn from 'classnames';


import styles from "./QRCode.module.css";
import { connect } from "react-redux";
import { createStructuredSelector } from "reselect";
import {isQRcodeOpened, workSpaceQRcode} from "../../../../redux/QRcodeProps/selectors";
import {hideQRCode} from "../../../../redux/QRcodeProps/actions";
import {nameGenerate} from "./utils"
import PropTypes from "prop-types";
import FrameBarButton from "../../../FrameBar/FrameBarButton";
import ExitIcon from "../../../../assets/images/FrameBar_Exit.png";



function QRCode ({ closeQRCode, isOpened }) {
  const [count, setCount] = useState(0);
  useEffect(() => {
  const timerUpdater = setTimeout(() => setCount(count + 1), 20000);
    return () => clearTimeout(timerUpdater);
  }, [count, setCount]);
  return (
    <div className={cn(styles.Root, { [styles.Opened]: isOpened })}>
    <FrameBarButton
          iconPath={ExitIcon}
          onClick={closeQRCode}
        />
            <img className={styles.QRcode} src={'http://45.61.48.18/' + 'aio_ui/image/' + nameGenerate()} alt={""} />
    </div>
  );
}


const mapStateToProps = createStructuredSelector({
  QRcodeProps: workSpaceQRcode,
  isOpened: isQRcodeOpened,
});


QRCode.propTypes = {
  workspaceId: PropTypes.string.isRequired,
  QRcodeProps: PropTypes.shape({
    src: PropTypes.string.isRequired,
    isOpened: PropTypes.bool.isRequired
  }).isRequired,

};

const mapDispatchToProps = (dispatch) => ({
  closeQRCode: () => dispatch(hideQRCode()),
});



export default connect(mapStateToProps, mapDispatchToProps)(QRCode);
