import { createSelector } from 'reselect';

export const specDialogProps = createSelector(
  (state) => state,
  (state) => state.SpecDialog
);

export const specDialogContentSelector = createSelector(
  specDialogProps,
  (state) => state.WorkSpacesProps,
  (dialogProps, workspacesProps) => {
    const contextRenderProps =
      workspacesProps?.byId[dialogProps.workspaceId]?.dialogProps
        ?.specifications;

    if (contextRenderProps) {
      return {
        contentStyle: contextRenderProps.style,
        buttonGroups: contextRenderProps.buttonGroups,
        defaults: contextRenderProps.default,
      };
    } else {
      return null;
    }
  }
);

export const specDialogContentStyleSelector = createSelector(
  specDialogContentSelector,
  (renderProps) => renderProps?.contentStyle || 'default'
);

export const specDialogBtnGroupsSelector = createSelector(
  specDialogContentSelector,
  (renderProps) => renderProps?.buttonGroups
);

export const specDialogDefaultsSelector = createSelector(
  specDialogContentSelector,
  (renderProps) => renderProps?.default
);
