import React from "react";
import cn from "classnames";

import styles from "./Label.module.css";
import { connect } from "react-redux";
import { createStructuredSelector } from "reselect";
import { prepareValue } from "../../../Navigation/InfoWidgets/InfoWidget/InfoTable/InfoCell/Row/utils";
import { workspaceDatasetsPropsSelector } from "../../../../redux/DatasetsProps/selectors";
import PropTypes from "prop-types";

const checkWarn = (value, restrictions) => {
  if ([" ~ ", ""].includes(value)) return true;
  return Number(value) < restrictions.min || Number(value) > restrictions.max;
};

const Label = ({ props, datasets }) => {
  const dataset = datasets[props.datasetId]?.dataset || {};
  const value = prepareValue(dataset, props.tagId, null, props.texted);
  const resText = props.text + value + props.postfix;
  const warnStatus = props.restrictions ? checkWarn(value, props.restrictions) : false;

  return (
    <div
      className={cn(styles.Root, {
        [styles.Shown]: true,
        [styles.Warn]: warnStatus,
      })}
      style={{
        top: `${props.pos.y}px`,
        left: `${props.pos.x}px`,
        fontSize: `${props.fontSize}px`,
      }}
    >
      {props.capitalize ? resText.toUpperCase() : resText}
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  datasets: workspaceDatasetsPropsSelector,
});

Label.propTypes = {
  props: PropTypes.shape({
    datasetId: PropTypes.string,
    tagId: PropTypes.string,
    text: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    capitalize: PropTypes.bool,
    pos: PropTypes.shape({
      x: PropTypes.number,
      y: PropTypes.number,
    }).isRequired,
    texted: PropTypes.objectOf(PropTypes.string),
  }),
  workspaceId: PropTypes.string.isRequired,
  datasets: PropTypes.object,
};

export default connect(mapStateToProps)(Label);
