import React from "react";
import { connect } from "react-redux";
import { createStructuredSelector } from "reselect";
import { labelsBySchemeIdSelector } from "../../../redux/WorkSpacesProps/selectors";
import Label from "./Label";
import PropTypes from "prop-types";

const Labels = ({ labels, workspaceId }) => {
  return (
    <>
      {labels.map((labelProps) => (
        <Label
          key={labelProps.tagId}
          workspaceId={workspaceId}
          props={labelProps}
        />
      ))}
    </>
  );
};

const mapStateToProps = createStructuredSelector({
  labels: labelsBySchemeIdSelector,
});

Labels.propTypes = {
  labels: PropTypes.arrayOf(PropTypes.object),
  workspaceId: PropTypes.string.isRequired,
};

export default connect(mapStateToProps)(Labels);
