import React from "react";
import Plotly from "plotly.js";
import createPlotlyComponent from "react-plotly.js/factory";

import { configuration } from "./config";
import ModeBar from "./ModeBar";

import styles from "./Chart.module.css";
import { connect } from "react-redux";
import { createStructuredSelector } from "reselect";
import {
  plotDatasetIdByIdSelector,
  plotPeriodByIdSelector,
  plotStreamByIdSelector,
  plotTargetsByIdSelector,
  plotTraceConf,
} from "../../../../../redux/PlotsProps/selectors";
import { getLastPointIndex, prepareTraceData, prepareXAxis } from "./utils";
import ModeBarButtons from "plotly.js/src/components/modebar/buttons";
import { workspaceDatasetsPropsSelector } from "../../../../../redux/DatasetsProps/selectors";
import { workspaceTagsSelector } from "../../../../../redux/WorkSpacesProps/selectors";
import PropTypes from "prop-types";

const Plot = createPlotlyComponent(Plotly);

const isHovered = {};

// TODO: Fix chart axis precision

class Chart extends React.Component {
  constructor(props) {
    super(props);
    this.state = configuration();
    isHovered[this.props.plotId] = false;
  }

  updateTooltips() {
    setTimeout(() => {
      this.onUnhover();
    }, 10);
  }

  resetPlotScale() {
    setTimeout(() => {
      ModeBarButtons.resetScale2d.click(
        this.state.graphDiv,
        {},
        "zoom",
        "reset"
      );
    }, 10);
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    const data = prevProps.datasets[this.props.currentDatasetId].dataset;

    // For initial dataset update
    if (!Object.keys(data || {}).length) {
      this.updateTooltips();
    }
    // For targets change
    if (
      JSON.stringify(prevProps.currentTargets) !==
      JSON.stringify(this.props.currentTargets)
    ) {
      this.updateTooltips();
      if (JSON.stringify(this.props.currentTargets) === '["","","",""]') {
        this.setYAxisRange([0, 100]);
      }
    }
    // For period change
    if (prevProps.currentPeriod !== this.props.currentPeriod) {
      this.setXAxisRange();
      this.resetPlotScale();
      this.updateTooltips();
    }
    // For stream change
    if (prevProps.currentStream !== this.props.currentStream) {
      this.resetPlotScale();
    }
    // For update when activeReset === false
    if (!this.state.activeReset && !isHovered[this.props.plotId]) {
      this.updateTooltips();
    }
  }

  onInitialized = (_, graphDiv) => {
    document
      .getElementById(`${this.props.plotId}-plot-root`)
      .addEventListener("mousemove", (ev) => {
        const absYPos = ev.pageY;
        const absXPos = ev.pageX;
        const plotRoot = document.getElementById(
          `${this.props.plotId}-plot-root`
        );

        const plotsBounds = {
          left: {
            left: 1125,
            right: 2578,
          },
          right: {
            left: 2893,
            right: 4345,
          },
        };

        const leftBound = plotsBounds[this.props.plotId].left;
        const rightBound = plotsBounds[this.props.plotId].right;

        const leftBoundCond = absXPos > leftBound;
        const rightBoundCond = absXPos < rightBound;
        const topBoundCond = absYPos > 2065;
        const bottomBoundCond = absYPos < 2655;

        if (
          leftBoundCond &&
          rightBoundCond &&
          topBoundCond &&
          bottomBoundCond
        ) {
          plotRoot.style.zIndex = "3";
          isHovered[this.props.plotId] = true;
        } else {
          plotRoot.style.zIndex = "1";
          this.onUnhover();
          isHovered[this.props.plotId] = false;
        }
      });
    this.onUnhover();
    this.setState((state) => {
      return {
        ...state,
        graphDiv: graphDiv,
      };
    });
  };

  setYAxisRange = (range) => {
    this.setState((state) => {
      return {
        ...state,
        layout: {
          ...state.layout,
          yaxis: {
            ...state.layout.yaxis,
            range: range,
          },
        },
      };
    });
  };

  setXAxisRange = (period = this.props.currentPeriod, ignore = true) => {
    if (ignore) return;

    const datasetLength = prepareXAxis(
      this.props.datasets[this.props.currentDatasetId].dataset,
      1440
    ).length;
    const startTick = datasetLength - period;
    const endTick = datasetLength - 1;

    try {
      Plotly.relayout(this.state.graphDiv, "xaxis.range", [
        startTick < 0 ? 0 : startTick,
        endTick,
      ]);
    } catch (err) {}
  };

  setReset = (reset) => {
    this.setState((state) => {
      return {
        ...state,
        activeReset: reset,
      };
    });
  };

  onUnhover = () => {
    const evt = this.props.currentTargets.map((_, index) => ({
      curveNumber: index,
      pointNumber: getLastPointIndex(
        this.props.datasets[this.props.currentDatasetId].dataset,
        this.props.currentPeriod
      ),
    }));
    Plotly.Fx.hover(`${this.props.plotId}-plot`, evt);
  };

  onRelayout = (eventData) => {
    const keys = JSON.stringify(Object.keys(eventData));
    switch (keys) {
      // For xy-zoom or pan
      case '["xaxis.range[0]","xaxis.range[1]","yaxis.range[0]","yaxis.range[1]"]':
        this.setReset(true);
        break;
      // For x-zoom
      case '["xaxis.range[0]","xaxis.range[1]"]':
        this.setReset(true);
        break;
      // For y-zoom
      case '["yaxis.range[0]","yaxis.range[1]"]':
        this.setReset(true);
        break;
      // For reset button
      case '["xaxis.autorange","xaxis.showspikes","yaxis.autorange","yaxis.showspikes"]':
        this.setReset(false);
        this.setXAxisRange();
        this.onUnhover();
        break;
      // For dbclick action
      case '["xaxis.autorange","yaxis.autorange"]':
        this.setReset(false);
        this.setXAxisRange();
        break;
      default:
        break;
    }
  };

  render() {
    let traces = null;
    if (JSON.stringify(this.props.currentTargets) === '["","","",""]') {
      traces = [
        {
          ...this.state.data[0],
          x: prepareXAxis(
            this.props.datasets[this.props.currentDatasetId].dataset,
            this.props.currentPeriod
          ),
          y: Array(1440).fill(null),
          text: "",
        },
      ];
    } else {
      traces =
        this.props.currentTargets &&
        this.props.currentTargets.map((target, index) => ({
          ...this.state.data[index],
          x: prepareXAxis(
            this.props.datasets[this.props.currentDatasetId].dataset,
            this.props.currentPeriod
          ),
          y: prepareTraceData(
            this.props.datasets[this.props.currentDatasetId].dataset,
            target,
            this.props.currentPeriod,
            this.props.traceConf
          ),
          text: this.props.targetsProps.byId[target]
            ? this.props.targetsProps.byId[target].text
            : "",
        }));
    }

    return (
      <>
        <ModeBar
          activeReset={this.state.activeReset}
          graphDiv={this.state.graphDiv}
        />
        <div id={`${this.props.plotId}-plot-root`} className={styles.Root}>
          <Plot
            divId={`${this.props.plotId}-plot`}
            data={traces}
            layout={this.state.layout}
            frames={this.state.frames}
            config={this.state.config}
            onInitialized={this.onInitialized}
            onRelayout={this.onRelayout}
          />
        </div>
      </>
    );
  }
}

const mapStateToProps = createStructuredSelector({
  currentPeriod: plotPeriodByIdSelector,
  currentTargets: plotTargetsByIdSelector,
  currentStream: plotStreamByIdSelector,
  currentDatasetId: plotDatasetIdByIdSelector,
  datasets: workspaceDatasetsPropsSelector,
  targetsProps: workspaceTagsSelector,
  traceConf: plotTraceConf,
});

Chart.propTypes = {
  plotId: PropTypes.string.isRequired,
  currentTargets: PropTypes.arrayOf(PropTypes.string).isRequired,
  currentPeriod: PropTypes.number.isRequired,
  currentStream: PropTypes.string.isRequired,
  currentDatasetId: PropTypes.string.isRequired,
  traceConf: PropTypes.shape({
    smooth_win: PropTypes.oneOfType([PropTypes.number]),
    excluded_tags: PropTypes.arrayOf(PropTypes.string).isRequired,
  }),
  datasets: PropTypes.objectOf(
    PropTypes.shape({
      dataset: PropTypes.object.isRequired,
    })
  ).isRequired,
  targetsProps: PropTypes.shape({
    byId: PropTypes.objectOf(
      PropTypes.shape({
        text: PropTypes.string.isRequired,
      })
    ).isRequired,
  }).isRequired,
};

export default connect(mapStateToProps)(Chart);
