import React from "react";
import QRCode from "./QRCode";
import styles from "./QRCodes.module.css";
import { createStructuredSelector } from "reselect";
import { connect } from "react-redux";
import { openQRCode } from "../../../redux/QRcodeProps/actions";
import QrcodeIcon from '../../../assets/images/App_Qrcode1.png';
import PropTypes from "prop-types";
import { appWorkspace } from "../../../redux/AppProps/selectors";


const QRCodes = ({ openQRCodeWindow, currWorkspace }) => {

  return (
    <div className={styles.QrcodeContainer}>
    <button onClick={openQRCodeWindow} className={styles.Qrcode}>
        <img src={QrcodeIcon} alt="" />
      </button>
       <QRCode workspaceId={currWorkspace} />
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  currWorkspace: appWorkspace,
});

const mapDispatchToProps = (dispatch) => ({
  openQRCodeWindow: () => dispatch(openQRCode()),
});

QRCodes.propTypes = {
  openQRCodeWindow: PropTypes.func.isRequired,
  currWorkspace: PropTypes.string.isRequired,
};

export default connect(mapStateToProps, mapDispatchToProps)(QRCodes);
