import React from 'react';
import cn from 'classnames';
import ReactTooltip from 'react-tooltip';
import styles from './Target.module.css';
import './TargetTooltip.css';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { plotTargetsByIdSelector } from '../../../../../../redux/PlotsProps/selectors';
import { setPlotTargets } from '../../../../../../redux/PlotsProps/actions';
import { workspaceTagsSelector } from '../../../../../../redux/WorkSpacesProps/selectors';
import PropTypes from 'prop-types';

const markerColors = {
  '-1': 'rgba(255,83,73, 0)',
  0: 'rgb(255,83,73)',
  1: 'rgb(249, 132, 114)',
  2: 'rgb(255, 184, 164)',
  3: 'rgb(255, 236, 179)',
};

const Target = ({ targetId, currPlotTargets, tagsProps, setTags }) => {
  const indexOfTarget = currPlotTargets.indexOf(targetId);
  const indexOfEmptySlot = currPlotTargets.indexOf('');
  const tagProps = tagsProps.byId[targetId];

  const handleClick = () => {
    const tmp = currPlotTargets.slice();

    if (indexOfTarget >= 0) {
      tmp[indexOfTarget] = '';
    } else {
      if (indexOfEmptySlot >= 0) tmp[indexOfEmptySlot] = targetId;
    }

    setTags(tmp);
  };

  if (tagProps == null) return null;

  return (
    <>
      <button
        data-tip={tagsProps.byId[targetId].description}
        className={cn(styles.Root, {
          [styles.Disabled]: indexOfEmptySlot < 0 && indexOfTarget < 0,
        })}
        onClick={handleClick}
      >
        <svg
          className={cn(styles.Marker, {
            [styles.checkedMarker]: indexOfTarget >= 0,
          })}
          width="51px"
          height="45px"
          viewBox="0 0 51 45"
        >
          <g transform="matrix(1,0,0,1,-2785.93,-2818.93)">
            <g transform="matrix(1,0,0,1,2801,2841.18)">
              <path
                d="M0,-0.35L6.796,6.825L20,-7.175"
                style={{
                  fill: 'none',
                  fillRule: 'nonzero',
                  stroke: markerColors[`${indexOfTarget}`],
                  strokeWidth: '3px',
                }}
              />
            </g>
          </g>
        </svg>
        <p className={styles.Text}>{tagsProps.byId[targetId].text || '###'}</p>
      </button>
      <ReactTooltip
        arrowColor={'rgba(0, 0, 0, 0)'}
        backgroundColor={'#353535'}
        textColor={'#9f9f9f'}
        place={'top'}
        effect={'solid'}
        delayShow={500}
        className={'targetTooltip'}
      />
    </>
  );
};

const mapStateToProps = createStructuredSelector({
  currPlotTargets: plotTargetsByIdSelector,
  tagsProps: workspaceTagsSelector,
});

const mapDispatchToProps = (dispatch, ownProps) => ({
  setTags: (tags) =>
    dispatch(setPlotTargets(ownProps.plotId, tags, ownProps.workspaceId)),
});

Target.propTypes = {
  targetId: PropTypes.string.isRequired,
  currPlotTargets: PropTypes.arrayOf(PropTypes.string).isRequired,
  tagsProps: PropTypes.shape({
    byId: PropTypes.objectOf(
      PropTypes.shape({
        text: PropTypes.string.isRequired,
        description: PropTypes.string.isRequired,
      }).isRequired
    ).isRequired,
  }).isRequired,
  setTags: PropTypes.func.isRequired,
};

export default connect(mapStateToProps, mapDispatchToProps)(Target);
