import React from "react";
import styles from "./Targets.module.css";
import Target from "./Target";
import { connect } from "react-redux";
import { createStructuredSelector } from "reselect";
import { plotStreamByIdSelector } from "../../../../../redux/PlotsProps/selectors";
import { workSpacePlotStreams } from "../../../../../redux/WorkSpacesProps/selectors";
import PropTypes from "prop-types";

const Targets = ({ plotId, workspaceId, streamsProps, currentStream }) => {
  const targetsList = streamsProps ? streamsProps.byId[currentStream].tags : [];

  return (
    <div className={styles.Root}>
      {targetsList.map((item) => (
        <Target
          key={item}
          targetId={item}
          plotId={plotId}
          workspaceId={workspaceId}
        />
      ))}
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  streamsProps: workSpacePlotStreams,
  currentStream: plotStreamByIdSelector,
});

Targets.propTypes = {
  plotId: PropTypes.string.isRequired,
  workspaceId: PropTypes.string.isRequired,
  streamsProps: PropTypes.shape({
    byId: PropTypes.objectOf(
      PropTypes.shape({
        tags: PropTypes.arrayOf(PropTypes.string).isRequired,
      })
    ).isRequired,
  }).isRequired,
  currentStream: PropTypes.string.isRequired,
};

export default connect(mapStateToProps)(Targets);
