import React from 'react';
import cn from 'classnames';

import styles from './ShutdownDialog.module.css';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { isShutdownDialogOpened } from '../../../redux/ShutdownDialog/selectors';
import { closeShutdownDialog } from '../../../redux/ShutdownDialog/actions';
import {useTranslation} from "react-i18next";

const ShutdownDialog = ({ isOpened, closeDialog, appExit }) => {
  const { t } = useTranslation();
  return (
    <div className={cn(styles.Root, { [styles.Opened]: isOpened })}>
      <div className={styles.Blocker} onClick={closeDialog} />
      <div className={styles.Content}>
        <span className={styles.Title}>{t(`Dialogs.ShutDownDialog.title`)}</span>
        <div className={styles.Buttons}>
          <button className={styles.RedButton} onClick={appExit}>
            {t(`Dialogs.ShutDownDialog.acceptBtn`)}
          </button>
          <button className={styles.WhiteButton} onClick={closeDialog}>
            {t(`Dialogs.ShutDownDialog.cancelBtn`)}
          </button>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  isOpened: isShutdownDialogOpened,
});

const mapDispatchToProps = (dispatch) => ({
  closeDialog: () => dispatch(closeShutdownDialog()),
});

export default connect(mapStateToProps, mapDispatchToProps)(ShutdownDialog);
