import { createSelector } from 'reselect';

const appPropertiesSelector = (state) => state.AppProps;

export const appWorkspace = createSelector(
  appPropertiesSelector,
  (appProps) => {
    return appProps?.currentWorkspace;
  }
);

export const appLoadedStatus = createSelector(
  appPropertiesSelector,
  (appProps) => {
    return appProps?.isLoaded;
  }
);

export const appLocale = createSelector(appPropertiesSelector, (appProps) => {
  return appProps?.locale || 'en';
});
