import { createSelector } from "reselect";

export const workspacesPropsSelector = createSelector(
  (state) => state.WorkSpacesProps,
  (props) => props
);

export const workspacesIdsSelector = createSelector(
  workspacesPropsSelector,
  (workspacesProps) => Object.keys(workspacesProps?.byId)
);

export const workSpacePropsByIdSelector = createSelector(
  workspacesPropsSelector,
  (_, { workspaceId }) => workspaceId,
  (workSpacesProps, workspaceId) => workSpacesProps?.byId[workspaceId]
);

export const workSpaceStatusTagSelector = createSelector(
  workSpacePropsByIdSelector,
  (props) => props?.status_tag
);

export const workSpaceLabelSelector = createSelector(
  workSpacePropsByIdSelector,
  (workspaceProps) => workspaceProps?.label || "Default"
);

export const workSpaceSchemes = createSelector(
  workSpacePropsByIdSelector,
  (workspaceProps) => workspaceProps?.schemes
);

export const advicesBySchemeIdSelector = createSelector(
  workSpaceSchemes,
  (_, { schemeId }) => schemeId,
  (schemesProps, schemeId) => schemesProps?.byId[schemeId]?.advices
);

export const labelsBySchemeIdSelector = createSelector(
  workSpaceSchemes,
  (_, { schemeId }) => schemeId,
  (schemesProps, schemeId) => schemesProps?.byId[schemeId]?.labels
);

export const workSpacePlotStreams = createSelector(
  workSpacePropsByIdSelector,
  (workspaceProps) => workspaceProps?.plotStreams
);

export const streamDefaultTags = createSelector(
  workSpacePlotStreams,
  (_, { streamId }) => streamId,
  (streamsProps, streamId) => streamsProps?.byId[streamId].defaultTags
);

export const streamDatasetId = createSelector(
  workSpacePlotStreams,
  (_, { streamId }) => streamId,
  (streamsProps, streamId) => streamsProps?.byId[streamId].datasetId
);

export const workspaceTagsSelector = createSelector(
  workSpacePropsByIdSelector,
  (workspaceProps) => workspaceProps?.plotTags
);

export const workspaceWidgetsSelector = createSelector(
  workSpacePropsByIdSelector,
  (workspaceProps) => workspaceProps?.infoWidgets || []
);

export const workspaceDialogPropsSelector = createSelector(
  workSpacePropsByIdSelector,
  (workspaceProps) => workspaceProps?.dialogProps || {}
);

export const workspaceSpecDefaultsSelector = createSelector(
  workspaceDialogPropsSelector,
  (dialogProps) => dialogProps?.specifications?.default
);
