import React, { useEffect, useState } from "react";
import styles from "./Recommendation.module.css";
import CrossImg from "../../../../assets/images/Rec_Cross.png";
import { combineText, countToTime, diffCheck, getDatasets, getStatus, getValues } from "./utils";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { createStructuredSelector } from "reselect";
import { recsTimerIntervalSelector } from "../../../../redux/RecProps/selectors";
import { datasetsPropsSelector } from "../../../../redux/DatasetsProps/selectors";

const Recommendation = ({ props, actionHandler, interval, datasets }) => {
  const [count, setCount] = useState(0);
  const data = getDatasets(datasets, props);
  const values = getValues(data, props);
  const diffCond = diffCheck(values, props.observe?.diff);
  const isStable = getStatus(data.optDataset, props.statusTag);
  let text = combineText(props.text, values.optValue, props.observe);

  useEffect(() => {
    const timerUpdater = setTimeout(() => setCount(count + 1), interval);
    return () => clearTimeout(timerUpdater);
  }, [count, setCount, interval]);

  useEffect(() => {
    setCount(0);
  }, [props.text]);

  if (text == null || diffCond) {
    actionHandler("DECLINED", props.id);
    return null;
  }

  if (props.statusTag && !isStable) {
    actionHandler("DECLINED", props.id);
    return null;
  }

  return (
    <div className={styles.Root}>
      <span className={styles.Timer}>{countToTime(count)}</span>
      <button className={styles.CloseButton} onClick={() => actionHandler("DECLINED", props.id)}>
        <img src={CrossImg} alt="Cross" />
      </button>
      <h1 className={styles.Text}>{text}</h1>
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  interval: recsTimerIntervalSelector,
  datasets: datasetsPropsSelector,
});

Recommendation.propTypes = {
  actionHandler: PropTypes.func.isRequired,
  interval: PropTypes.number.isRequired,
  props: PropTypes.shape({
    id: PropTypes.string.isRequired,
    text: PropTypes.string,
  }).isRequired,
};

export default connect(mapStateToProps)(Recommendation);
