const initialState = {
  style: "default",
  isShown: false,
  tagId: null,
  demo: {
    initValue: null,
    increment: null,
    updateInterval: null,
  },
};

const reducer = (state = initialState, action) => {
  const { type } = action;

  switch (type) {
    default:
      return state;
  }
};

export default reducer;
