import React from "react";

import styles from "./Advice.module.css";
import { createStructuredSelector } from "reselect";
import { connect } from "react-redux";
import { getLastPoint, getStatus } from "./utils";
import { workspaceDatasetsPropsSelector } from "../../../../redux/DatasetsProps/selectors";
import PropTypes from "prop-types";
import { recsValuesSelector } from "../../../../redux/RecProps/selectors";
import { workSpaceStatusTagSelector } from "../../../../redux/WorkSpacesProps/selectors";
import cn from "classnames";

const Advice = ({ adviceProps, datasets, recValues, workspaceStatusTag }) => {
  const {
    label,
    optTagId,
    realTagId,
    precision,
    units,
    margin,
    datasetsId,
    recValId,
    statusTag,
  } = adviceProps;
  const optDataset = datasets[datasetsId["opt"]].dataset || {};
  const realDataset = datasets[datasetsId["real"]].dataset || {};
  const workspaceStatus = getStatus(optDataset, workspaceStatusTag);
  const adviceStatus = getStatus(optDataset, statusTag);

  return (
    <div className={styles.Root} style={{ left: `${margin}px` }}>
      <span className={styles.Name}>{label}</span>
      <span
        className={cn(styles.OptVal, {
          [styles.Hidden]: !workspaceStatus || !adviceStatus,
        })}
      >
        <span className={styles.OptMarker}>OPT</span>
        <span>
          {getLastPoint(
            recValId ? recValues[recValId] : optDataset,
            optTagId,
            precision
          )}
        </span>
        <span className={styles.units}>{` ${units}`}</span>
      </span>
      <span className={styles.RealVal}>
        <span className={styles.RealMarker}>REAL</span>
        <span>{getLastPoint(realDataset, realTagId, precision)}</span>
        <span className={styles.units}>{` ${units}`}</span>
      </span>
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  datasets: workspaceDatasetsPropsSelector,
  recValues: recsValuesSelector,
  workspaceStatusTag: workSpaceStatusTagSelector,
});

Advice.propTypes = {
  adviceProps: PropTypes.shape({
    label: PropTypes.string.isRequired,
    optTagId: PropTypes.string.isRequired,
    realTagId: PropTypes.string.isRequired,
    precision: PropTypes.number.isRequired,
    units: PropTypes.string.isRequired,
    margin: PropTypes.number.isRequired,
    statusTag: PropTypes.string,
    datasetsId: PropTypes.shape({
      opt: PropTypes.string.isRequired,
      real: PropTypes.string.isRequired,
    }).isRequired,
  }).isRequired,
  datasets: PropTypes.objectOf(
    PropTypes.shape({
      dataset: PropTypes.object.isRequired,
    })
  ).isRequired,
  recValues: PropTypes.object.isRequired,
  workspaceStatusTag: PropTypes.string,
};

export default connect(mapStateToProps)(Advice);
