import React from "react";
import styles from "./SpecInput.module.css";
import cn from "classnames";
import "./SpecInput.css";
import { connect } from "react-redux";
import { setConstants } from "../../../../../redux/SpecDialog/actions";
import PropTypes from "prop-types";

const validateInput = (input, validationProps) => {
  try {
    const { inputType, restrictions } = validationProps;
    if (String(input).length > restrictions.len) return false;
    if (inputType === "number") {
      const inputNumber = Number(input);
      return !(
        inputNumber < restrictions.min || inputNumber > restrictions.max
      );
    }

    return true;
  } catch (err) {
    window?.api?.log?.error("\nValidate input error!");
    window?.api?.log?.error(err);
    return false;
  }
};

const SpecInput = ({
  spec,
  text,
  postfix,
  currSpecs,
  setSpecs,
  tagId,
  validation,
  step,
}) => {
  const updateSpec = (ev) => {
    const value = ev.target.value;
    if (!validateInput(value, validation)) return;

    setSpecs({
      ...currSpecs,
      [tagId]: value,
    });
  };

  return (
    <div className={styles.Root}>
      <label className={styles.Text}>{text}</label>
      <input
        className={cn({
          [styles.Invalid]: !spec || !validateInput(spec, validation),
        })}
        id={`${spec}-spec-input`}
        type={validation.inputType}
        step={step}
        value={spec}
        onChange={updateSpec}
      />
      <label
        className={cn({
          [styles.InvalidLabel]: !spec || !validateInput(spec, validation),
        })}
        htmlFor={`${spec}-spec-input`}
      >
        {postfix}
      </label>
    </div>
  );
};

const mapDispatchToProps = (dispatch) => ({
  setSpecs: (specs) => dispatch(setConstants(specs)),
});

SpecInput.propTypes = {
  spec: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,
  text: PropTypes.string.isRequired,
  currSpecs: PropTypes.objectOf(
    PropTypes.oneOfType([PropTypes.string, PropTypes.number])
  ).isRequired,
  setSpecs: PropTypes.func.isRequired,
  tagId: PropTypes.string.isRequired,
  validation: PropTypes.object.isRequired,
};

export default connect(null, mapDispatchToProps)(SpecInput);
