import React from 'react';
import cn from 'classnames';
import { useTranslation } from 'react-i18next';
import styles from './DownloadDialog.module.css';
import { Divider } from '@material-ui/core';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import {
  downloadContextBtnGroupsSelector,
  downloadContextRenderOrderSelector,
  downloadPropsSelector,
} from '../../../../redux/DownloadProps/selectors';
import {
  closeDownloadDialog,
  downloadData,
  setDownloadValueByType,
} from '../../../../redux/DownloadProps/actions';
import RadioBtnsGroup from '../RadioBtnsGroup';
import PropTypes from 'prop-types';

const Dvider = () => {
  return (
    <div className={styles.Divider}>
      <Divider />
    </div>
  );
};

const DownloadBtn = ({ downloadHandler, closeHandler }) => {
  return (
    <div className={styles.Buttons}>
      <button onClick={downloadHandler} className={styles.WhiteButton}>
        
      </button>
      <button onClick={closeHandler} className={styles.WhiteButton}>
        
      </button>
    </div>
  );
};

const DownloadDialog = ({
  dialogProps,
  renderOrder,
  buttonGroups,
  setValue,
  closeDialog,
  download,
}) => {
  const { t } = useTranslation();
  const handleDownload = () => {
    download();
    closeDialog();
  };

  if (dialogProps.workspaceId == null) {
    return (
      <div
        className={cn(styles.Root, { [styles.Opened]: dialogProps.isOpened })}
      >
        <div className={styles.Blocker} onClick={closeDialog} />
      </div>
    );
  }

  return (
    <div className={cn(styles.Root, { [styles.Opened]: dialogProps.isOpened })}>
      <div className={styles.Blocker} onClick={closeDialog} />
      <div className={styles.Content}>
        <h1 className={styles.Title}>{t(`Dialogs.DownloadDialog.title`)}</h1>
        <div className={styles.GroupsContainer}>
          {renderOrder.map((groupType, index) => {
            return (
              <div key={groupType}>
                <RadioBtnsGroup
                  key={groupType}
                  handler={setValue}
                  storeValues={dialogProps.values}
                  props={buttonGroups[groupType]}
                />
                {index !== renderOrder.length - 1 && <Dvider />}
              </div>
            );
          })}
        </div>
        <DownloadBtn
          downloadHandler={handleDownload}
          closeHandler={closeDialog}
        />
      </div>
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  dialogProps: downloadPropsSelector,
  renderOrder: downloadContextRenderOrderSelector,
  buttonGroups: downloadContextBtnGroupsSelector,
});

const mapDispatchToProps = (dispatch) => ({
  setValue: (value, type) => dispatch(setDownloadValueByType(value, type)),
  closeDialog: () => dispatch(closeDownloadDialog()),
  download: () => dispatch(downloadData()),
});

DownloadDialog.propTypes = {
  dialogProps: PropTypes.shape({
    workspaceId: PropTypes.string,
    isOpened: PropTypes.bool.isRequired,
    values: PropTypes.object.isRequired,
  }).isRequired,
  renderOrder: PropTypes.arrayOf(PropTypes.string),
  buttonGroups: PropTypes.object,
  setValue: PropTypes.func.isRequired,
  closeDialog: PropTypes.func.isRequired,
  download: PropTypes.func.isRequired,
};

export default connect(mapStateToProps, mapDispatchToProps)(DownloadDialog);
