import produce from "immer";
import {
  CLOSE_DOWNLOAD_DIALOG,
  DOWNLOAD_DATA,
  FAILURE,
  OPEN_DOWNLOAD_DIALOG,
  SET_DOWNLOAD_VALUE,
  SUCCESS,
} from "../constants";

const initialState = {
  isOpened: false,
  workspaceId: null,
  apiEndpoint: "/download?",
  error: null,
  values: {
    data: "all_data",
    period: "1",
  },
};

const reducer = (state = initialState, action) =>
  produce(state, (draft) => {
    const { type, payload, error } = action;

    switch (type) {
      case OPEN_DOWNLOAD_DIALOG:
        draft.isOpened = true;
        draft.workspaceId = payload.workspaceId;
        break;
      case CLOSE_DOWNLOAD_DIALOG:
        draft.isOpened = false;
        draft.workspaceId = null;
        break;
      case SET_DOWNLOAD_VALUE:
        draft.values[payload.type] = payload.value;
        break;
      case DOWNLOAD_DATA + SUCCESS:
        draft.error = null;
        break;
      case DOWNLOAD_DATA + FAILURE:
        draft.error = error;
        break;
      default:
        return;
    }
  });

export default reducer;
