import { createSelector } from "reselect";

export const datasetsPropsSelector = createSelector(
  (state) => state.DatasetsProps,
  (props) => props
);

export const datasetsByWorkspaceIdSelector = createSelector(
  datasetsPropsSelector,
  (_, { workspaceId }) => workspaceId,
  (workspacesDatasetsProps, workspaceId) => workspacesDatasetsProps[workspaceId]
);

export const workspaceDatasetsIdsSelector = createSelector(
  datasetsByWorkspaceIdSelector,
  (workspacesDatasetsProps) => Object.keys(workspacesDatasetsProps?.byId)
);

export const workspaceDatasetsPropsSelector = createSelector(
  datasetsByWorkspaceIdSelector,
  (datasetsProps) => datasetsProps?.byId
);
