import FrameBar from "../FrameBar";
import PropTypes from "prop-types";
import React, { useEffect } from "react";
import Workspace from "../Workspace";
import styles from "./App.module.css";
import { appDatasetsUpdater, appRecommendationsUpdater } from "./utils";
import { appLoadedStatus, appLocale } from "../../redux/AppProps/selectors";
import { connect } from "react-redux";
import { createStructuredSelector } from "reselect";
import { datasetsPropsSelector } from "../../redux/DatasetsProps/selectors";
import { loadDataset } from "../../redux/DatasetsProps/actions";
import { recsAPIsSelector } from "../../redux/RecProps/selectors";
import { requestCalculation as datasetCalculator } from "../../utils";
import { requestNewRecs } from "../../redux/RecProps/actions";
import { useTranslation } from "react-i18next";
import { workspacesIdsSelector } from "../../redux/WorkSpacesProps/selectors";
import Navigation from "../Navigation";

export function App({
  workspacesIds,
  datasetsProps,
  recsProps,
  isLoaded,
  locale,
  updateDataset,
  updateRec,
}) {
  const { i18n } = useTranslation();
  i18n.changeLanguage(locale || "en").catch((err) => {
    window?.api?.log?.error("\nSet locale error!");
    window?.api?.log?.error(err.message);
  });

  useEffect(() => {
    async function appUpdater() {
      if (!isLoaded) return;
      await appDatasetsUpdater(
        workspacesIds,
        datasetsProps,
        datasetCalculator,
        updateDataset
      );
      await appRecommendationsUpdater(recsProps, updateRec);
      window?.api?.send?.("appShow", true);
    }

    appUpdater().catch((err) => {
      window?.api?.log?.error("\nFailed to start app updater!");
      window?.api?.log?.error(err.message);
    });
  }, []); // eslint-disable-line

  return (
    <div className={styles.Root}>
      <FrameBar />
      {isLoaded && (
        <>
          <Navigation />
          <Workspace />
        </>
      )}
    </div>
  );
}

const mapStateToProps = createStructuredSelector({
  workspacesIds: workspacesIdsSelector,
  datasetsProps: datasetsPropsSelector,
  isLoaded: appLoadedStatus,
  locale: appLocale,
  recsProps: recsAPIsSelector,
});

const mapDispatchToProps = (dispatch) => ({
  updateDataset: (workspaceId, datasetProps) =>
    dispatch(loadDataset(workspaceId, datasetProps)),
  updateRec: (workspaceId, route) =>
    dispatch(requestNewRecs(workspaceId, route)),
});

function areEqual(prevProps, nextProps) {
  return prevProps.isLoaded === nextProps.isLoaded;
}

App.propTypes = {
  workspacesIds: PropTypes.arrayOf(PropTypes.string).isRequired,
  datasetsProps: PropTypes.objectOf(
    PropTypes.shape({
      byId: PropTypes.objectOf(
        PropTypes.shape({
          id: PropTypes.string.isRequired,
          apiEndpoints: PropTypes.shape({
            get: PropTypes.string.isRequired,
            update: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
          }).isRequired,
          updateInterval: PropTypes.number.isRequired,
        })
      ).isRequired,
    })
  ).isRequired,
  recsProps: PropTypes.arrayOf(
    PropTypes.shape({
      apiEndpoint: PropTypes.string.isRequired,
      updateInterval: PropTypes.number.isRequired,
      workspaceId: PropTypes.string.isRequired,
    })
  ).isRequired,
  isLoaded: PropTypes.bool.isRequired,
  locale: PropTypes.oneOf(["en", "ru"]).isRequired,
  updateDataset: PropTypes.func.isRequired,
  updateRec: PropTypes.func.isRequired,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(React.memo(App, areEqual));
