export function countToTime(count) {
  if (count === 0) return "";

  const days = Math.floor(count / 1440);
  const hours = Math.floor(count / 60);
  const minutes = count;
  let result = "";

  const checkPostfix = (count) => {
    return count > 1 ? "s " : " ";
  };

  if (days) {
    result += `${days} `;
    result += checkPostfix(days);
  }
  if (hours) {
    result += `${hours} .`;
    result += checkPostfix(hours);
  }

  result += `${minutes - hours * 60} . `;

  return result;
}

export function prepareValue(df, tagId, precision) {
  try {
    const rawValue = Object.values(df[tagId]).slice(-1)[0];
    return Number(rawValue).toFixed(precision);
  } catch (error) {
    return null;
  }
}

export const datasetGetter = (datasets, observeProps, workspaceId) => {
  if (observeProps == null) return null;

  try {
    const { datasetId } = observeProps;
    return datasets[workspaceId]?.byId[datasetId]?.dataset;
  } catch (err) {
    return null;
  }
};

export const valueGetter = (dataset, valueProps, precision) => {
  if (valueProps == null) return null;

  try {
    const { tagId } = valueProps;
    return prepareValue(dataset, tagId, precision);
  } catch (err) {
    return null;
  }
};

export const getDatasets = (datasets, recProps) => {
  return {
    optDataset: datasetGetter(datasets, recProps.observe, recProps.workspaceId),
    realDataset: datasetGetter(datasets, recProps.observe?.diff, recProps.workspaceId),
  };
};

export const getValues = (data, recProps) => {
  const precision = recProps.observe?.precision;
  return {
    optValue: valueGetter(data.optDataset, recProps.observe, precision),
    realValue: valueGetter(data.realDataset, recProps.observe?.diff, precision),
  };
};

export const diffCheck = (values, diffProps) => {
  if (diffProps == null) return false;

  try {
    const { condition } = diffProps;
    const { optValue, realValue } = values;
    return Math.abs(Number(optValue) - Number(realValue)) < condition;
  } catch (err) {
    return false;
  }
};

export const combineText = (text, value, observeProps) => {
  if (observeProps == null) {
    return text;
  } else {
    return value ? `${text}${value} ${observeProps.units}` : text;
  }
};

export function getStatus(dataset, targetId) {
  try {
    return Number(Object.values(dataset[targetId]).slice(-1)[0]);
  } catch (err) {
    return false;
  }
}
