export const requestNewRecs = (workspaceId, apiEndpoint) => async (dispatch) => {
  dispatch({
    type: REQUEST_NEW_RECS,
    payload: {},
    CallAPI: `${workspaceId}${apiEndpoint}`,
  });
};

export const hideRec = (recId) => ({
  type: CLOSE_REC,
  payload: { recId },
});

